<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
				<!-- 添加 start-->
				 <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">添加</el-button>
				<!-- 添加 end-->
      </el-header>
			<el-main>
			    <!-- 表格 start -->
			    <el-table class="table" :data="tableData" style="width: 100%" border v-loading="tableLoading" :header-cell-style="{
			        background: '#f2f2f2',
			        color: '#606266',
			        height: '38px',
			        padding: '0px'
			      }" :cell-style="{ height: '38px', padding: '0' }" :default-sort="{ prop: 'grossProfit', order: 'descending' }" @sort-change="onSortChange">
			      <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" align="center" min-width="130" :sortable="tableSortable(value)">
			        <template slot-scope="scope">
			          <div v-html="tableContentValue(scope.row, value)">
			          </div>
			        </template>
			      </el-table-column>
						<el-table-column label="操作" min-width="130">
						  <template slot-scope="scope">
								<el-button @click.native.prevent="editAtt(scope.$index, scope.row)" type="primary" size="small">
								  编辑
								</el-button>
								<el-button @click.native.prevent="stateAtt(scope.$index, scope.row)" :type="scope.row.status == 0?'danger':'primary'" size="small">
									{{scope.row.status == 0?'禁用':'启用'}}
								</el-button>
								<el-button @click.native.prevent="delAtt(scope.$index, scope.row)" type="info" size="small">
								  删除
								</el-button>
						  </template>
						</el-table-column>
			    </el-table>
			    <!-- 表格 end -->
			
			    <!-- 导出的表格 start -->
			    <!-- 此表格始终保持隐藏 -->
			   <el-table id="outTable" class="table" :data="exportTableData" style="width: 100%; display: none;" border>
			      <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130" align="center">
			      </el-table-column>
			    </el-table>
			    <!-- 导出的表格 end -->
			  </el-main>
			  <el-footer>
			    <!-- 分页 start -->
			    <div class="block">
			      <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="total" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
			      </el-pagination>
			    </div>
			    <!-- 分页 end -->
			  </el-footer>
				<!--考勤管理时间-->
				<el-dialog
				  :title="dialogTitle"
				  :visible.sync="centerDialogVisible"
					:before-close="handleClose"
					:close-on-click-modal="false"
					 width="50%"
				  center>
					<!-- <template> -->
						<!-- <div class="time-select"> -->
						<el-form
						:model="validateForm"
						ref="validateForm"
						label-width="100px"
						class="demo-ruleForm"
						:rules='validateFormRules'
						>
							<el-form-item label="开始时间" prop="startTimeRules">
								<el-time-select
								  placeholder="请输入开始时间"
								  v-model="validateForm.startTimeRules"
									@change="handStartTime"
								  :picker-options="{
								    start: '08:00',
								    step: '00:10',
								    end: '20:00'
								  }"
									class="time-select_startTime"
									>
								</el-time-select>
							</el-form-item>
							<el-form-item label="结束时间" prop="endTimeRules">
								<el-time-select
								  placeholder="请输入结束时间"
								  v-model="validateForm.endTimeRules"
									@change="handEndTime"
								  :picker-options="{
								    start: '08:00',							
								    step: '00:10',
								    end: '20:00',
								    minTime: validateForm.startTimeRules
								  }"
									class="time-select_endTime">
								</el-time-select>
							</el-form-item>
							<el-form-item label="创建人:" prop="creatorName" v-if="creator">
								<el-input v-model="validateForm.creatorName" style="width: 220px;margin-left: 50px;" placeholder="请输入创建人"></el-input>
							</el-form-item>
						</el-form>
						
						<!-- 提交 start -->
						<div class="submit">
							<el-button @click="cancelForm()">取消</el-button>
						  <el-button type="primary" @click="submitForm('validateForm')">提交</el-button>
						</div>
						<!-- 提交 end -->
				</el-dialog>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime, formatMonth ,formTime } from '../utils/utils.js'
export default {
  name: 'AttendanceTime',
  data () {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      tableFields: {
        //导出Excel表格的表头设置
        考勤时间: 'updateDate',
        状态: 'status',
        创建时间: 'createDate',
        创建人: 'creatorName',
      },
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      // searchScenicOptions: [], // 搜索的景区选项
			clockItem:[],//查询打卡时间
      // robotOptions: [], // 提交时的机器人选项
      companyOptions: [], // 提交时的公司选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      // importLoading: false, // 导入excel按钮的加载动画
			startTimeRules: '',//开始时间
			endTimeRules: '',//结束时间
      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数
      total: 0, // 总页数
      dates: '', // 统计时间
			dialogTitle: '',
      ountAttendancec: '', // 统计实际出勤人次
      tatisticalAttendances: '', // 统计出勤率
			// creatorName:'',
      dialogVisible: false, // 是否显示编辑dialog对话框
			creator:true,
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'
			dialogItem:true, //删除时间段落
			centerDialogVisible: false,//对话框开关
      dateType: 'daterange', // 日期类型（月，日，年）
			ItemType:'datetime',//时间类型（时，分，秒）

      searchValidateForm: {}, // 搜索表单验证对象

      validateForm: {}, // 表单验证对象
			validateFormTime: {},

      searchStartEndDate: '', // 搜索的起止日期
			searchStartEndItem:'',//搜索的起止时间
			selectDateState:'3',
			selectTimeState:'0',//传入时间状态值
      myPieChart: null,
      pieOption: {},


      robotId: '', // 当前点击行的 robotId
      statisticDialogVisible: false, // 设备统计dialgo显示状态

      // 搜索表单规则
      searchValidateFormRules: {},

      tableSort: '1,desc', // 表格排序

      // 提交表单规则
      validateFormRules: {
				//开始时间
				startTimeRules: [
				  {
				    validator: checkRequireMethod('开始时间'),
				    required: true,
				    trigger: ['blur', 'change']
				  }
				],
				//结束时间
				endTimeRules: [
				  {
				    validator: checkRequireMethod('结束时间'),
				    required: true,
				    trigger: ['blur', 'change']
				  }
				],
				//创建人
				creatorName: [
				  {
				    validator: checkRequireMethod('创建人'),
				    required: true,
				    trigger: ['blur', 'change']
				  }
				]
      }
    }
  },
  created () {
    this.getSearchValidateForm()
    // this.login()
    this.getCompanyList()
    // this.getScenicList()
    this.initTableData()
    // this.getRobotIdList()
  },
  beforeMount () { },
  mounted () {},
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm () {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        // scenicSpotId: '', // 景区id,
        // employeeType: '', // 职业类型
        ...validateForm
      }
    },

		// 获取表单验证对象
		getValidateForm() {
		  var validateForm = {}
		  for (var item in this.tableFields) {
		    validateForm[this.tableFields[item]] = ''
		  }
		  this.validateForm = validateForm
		  // console.log(this.validateForm);
		},
		
    // 登录(必须)
   //  async login () {
   //    const { data: res } = await this.$http.post(
   //      '/loginSystem?username=houjingchen&password=houjingchen'
   //    )
			// console.log(res)
   //  },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList (spotId) {
      // if (!spotId) {
      //   spotId = null
      // }
      // const { data: res } = await this.$http.get(
      //   '/system/robot_time_statistics/companyList',
      //   {
      //     params: {
      //       spotId: spotId
      //     }
      //   }
      // )
      // if (res.state == '200') {
      //   console.log(res)
      //   this.searchCompanyOptions = res.data
      //   this.companyOptions = res.data
      //   return res
      // } else {
      //   return
      // }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    // async getScenicList (companyId) {
    //   if (!companyId) {
    //     companyId = null
    //   }
    //   // 这里使用的是`签约信息`中的`景区列表`接口
    //   const { data: res } = await this.$http.get(
    //     '/system/robot_time_statistics/spotList',
    //     {
    //       params: {
    //         companyId: companyId
    //       }
    //     }
    //   )
    //   if (res.state == '200') {
    //     // console.log(res);
    //     this.searchScenicOptions = res.data
    //     return res
    //   } else {
    //     return
    //   }
    // },

    /**
     * 初始化表格数据
     */
    async initTableData () {
			// console.log(this.searchStartEndItem)
      this.tableLoading = true
      var startDate = null
      var endDate = null
			var startTime = null 
			var endTime = null
			console.log(this.searchStartEndDate)
      if (this.searchStartEndDate.length !== 0) {
        startDate = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endDate = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }
			//时，分
			if (this.searchStartEndItem.length !== 0) {
			  startTime = formTime(
			    this.searchStartEndItem[0],
			    this.selectTimeState
			  )
			  endTime = formTime(
			    this.searchStartEndItem[1],
			    this.selectTimeState
			  )
			}
			console.log()
      const { data: res } = await this.$http.get(
        '/sys/AttendanceRecord/selectAttendanceTime',
        {
          params: {
            // ...this.searchValidateForm,
						...this.searchValidateForm,
            // dateType: this.selectDateState,
      //       startDate,
      //       endDate,
						// startTime,
						// endTime,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
			console.log(res)
      this.tableData = res.data
			// console.log(this.tableData)
      this.total = res.total //总页数
      this.tableLoading = false
    },
		
		//点击更改状态
		stateAtt(index, row){
			console.log(row.id)
			this.$confirm('是否改变状态?', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
			        }).then(() => {
								if(row.status == '1'){
									this.searchValidateForm.status = 0
									var obj = this.searchValidateForm
									var value = row.id
									var key = 'id'
									obj[key] = value
									console.log(obj)
									this.stateTableData(obj)
								}else if(row.status == '0'){
									this.searchValidateForm.status = 1
									var obj = this.searchValidateForm
									var value = row.id
									var key = 'id'
									obj[key] = value
									console.log(obj)
									this.stateTableData(obj)
								}
								console.log('状态已更改')
			        }).catch(() => {
								console.log('已取消更改状态')  
			        });
		},
    /**
     * 新增表格数据
     */
     onAdd () {
			 this.creator = true
			this.centerDialogVisible = true
			this.dialogState = 'add'
      this.dialogTitle = '添加考勤管理时间'
    },
		
		cancelForm(){
			this.centerDialogVisible = false
			this.creator = false
			this.getValidateForm()
		},
			/**
			 * 修改表格数据
			 * 
			 */
			editAtt(index, row){
				console.log(row)
				this.creator = false
				// this.validateForm.endTimeRules = row.endDate
				// this.validateForm. startTimeRules= row.startDate
				var rowObj = Object.assign({},row)
				this.validateFormTime = rowObj
				this.centerDialogVisible = true
				this.dialogState = 'edit'
				this.dialogTitle = '编辑考勤管理时间'
			},

// 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },


	submitForm(){
		if(this.dialogState == 'add'){
			console.log('新增考勤管理时间')
			this.addTableData()
		}else if(this.dialogState == 'edit'){
			console.log('修改考勤管理时间')
			this.editTableData()
		}
		this.centerDialogVisible = false;
		this.dialogVisible = false
		this.$refs.validateForm.resetFields() // 重置表单
		this.getValidateForm()
	},

		/**
		 * 新增表格数据
		 */
		async addTableData () {
			// console.log(this.startTime)
			console.log(this.validateForm)
			var data = {
				startDate:this.validateForm.startTimeRules,
				endDate:this.validateForm.endTimeRules,
				creatorName:this.validateForm.creatorName
			}
			const { data: res } = await this.$http.post(
			  '/sys/AttendanceRecord/addAttendanceTime',this.$qs.stringify(data)
			)
			console.log(res)
			if (res.state == '200') {
			  this.$message({
			    message: res.msg,
			    type: 'success',
			    offset: 100,
			    center: true,
			    duration: 1000
			  })
				this.creatorName =''
				this.startDate =''
				this.endDate =''
			  this.initTableData()
			} else {
			  this.$message({
			    message: res.msg,
			    type: 'error',
			    offset: 100,
			    center: true,
			    duration: 3000
			  })
			}
		},
		
		/**
		 * 修改表格数据
		 */
		async editTableData () {
			var data ={
				startDate:this.validateForm.startTimeRules,
				endDate:this.validateForm.endTimeRules,
				creatorName:this.validateFormTime.creatorName,
				id:this.validateFormTime.id,
				status:this.validateFormTime.status
			}
		  const { data: res } = await this.$http.post(
		    '/sys/AttendanceRecord/upAttendanceTime',this.$qs.stringify(data)
		  )
			console.log(res)
		  if (res.state == '200') {
		    this.$message({
		      message: res.msg,
		      type: 'success',
		      offset: 100,
		      center: true,
		      duration: 1000
		    })
		    this.initTableData()
		  } else {
		    this.$message({
		      message: res.msg,
		      type: 'error',
		      offset: 100,
		      center: true,
		      duration: 3000
		    })
		  }
		},

		/**
			*修改状态
		 */
		
		async stateTableData(data){
			const { data: res } = await this.$http.post(
			  '/sys/AttendanceRecord/upAttendanceTime',this.$qs.stringify(data)
			)
			console.log(res)
			if (res.state == '200') {
				this.$message({
					type: 'success',
					message: '状态已更改!'
				});
			  this.initTableData()
			} else {
			this.$message({
			  type: 'info',
			  message: '更改状态失败'
			});   
			}
		},
    /**
     * 删除表格数据
     */
    async delAtt (index, row) {
      console.log(index,row)
      this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.get(
            '/sys/AttendanceRecord/deAttendanceTime',
            {
              params: {
                id: row.id
              }
            }
          )
          console.log(res);
          if (res.state == '200') {
            this.$message({
              type: 'success',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 1000
            })
            this.initTableData()
          } else {
            this.$message({
              type: 'error',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 3000
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData () {
			
			var startDate = null
			var endDate = null
			
			if (this.searchStartEndDate.length !== 0) {
			  startDate = formatDate(
			    this.searchStartEndDate[0],
			    this.selectDateState
			  )
			  endDate = formatDate(
			    this.searchStartEndDate[1],
			    this.selectDateState
			  )
			}
			
      const { data: res } = await this.$http.post(
        '/sys/AttendanceRecord/exportEmployeeAttendance',
        {
					...this.searchValidateForm,
					startDate,
					endDate
				},
        {
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
			console.log(a)
      a.href = url
      a.download = '员工考勤'
      a.click()
      this.downloadLoading = false
    },
	//考勤时间查询
	// dialogTime(){
	// 	console.log(this.clockItem)
	// 	this.centerDialogVisible = false
	// 	this.initTableData()
	// },
	// iconRemove(index){
	// 	this.clockItem.splice(index,1);
	// },
    // 景区选项变化时触发
    async onChangeSearchSpot (spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      // this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.ascriptionCompanyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.ascriptionCompanyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.ascriptionCompanyId =
          searchCompanyOptions[0].COMPANY_ID
      }
    },


    // 重置
    onReset () {
      // 重新获取表单
      this.getSearchValidateForm()
      this.currentPage = 1

      this.selectDateState = '3'
      this.dateType = 'daterange'
      this.searchStartEndDate = ''
