      // 重新获取搜索景区列表
      // this.getScenicList()
    },

		//获取开始时间
			handStartTime(evet){
				if(evet != null){
					this.startTime = evet
				}
			},
			//获取结束时间
			handEndTime(evet){
				if(evet != null){
						console.log( this.startTime)
						var startTime = this.startTime
						var endTime = this.endTime
						var arr = {
							index:this.index++,
							startTime:startTime,
							endTime:endTime
						}
						console.log(arr)
							if(arr.startTime != '' && arr.startTime != null){
								this.clockItem.push(arr)
							}
						}
			},

    // currentPage 改变时会触发
    onCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange (pageSize) {
      this.pageSize = pageSize
      this.initTableData()
    },
    // 当表格的排序条件发生变化的时候回触发改事件
    onSortChange ({ column, prop, order }) {
      console.log(column, prop, order)
      if (prop == 'grossProfit') {
        // 毛利
        if (order == 'descending') {
          this.tableSort = '1,desc'
        } else if (order == 'ascending') {
          this.tableSort = '1,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'runningAmount') {
        // 流水金额
        if (order == 'descending') {
          this.tableSort = '2,desc'
        } else if (order == 'ascending') {
          this.tableSort = '2,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'comprehensiveCost') {
        // 台均综合成本
        if (order == 'descending') {
          this.tableSort = '3,desc'
        } else if (order == 'ascending') {
          this.tableSort = '3,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'robotGrossProfit') {
        // 台均毛利
        if (order == 'descending') {
          this.tableSort = '4,desc'
        } else if (order == 'ascending') {
          this.tableSort = '4,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'netProfit') {
        // 净利润
        if (order == 'descending') {
          this.tableSort = '5,desc'
        } else if (order == 'ascending') {
          this.tableSort = '5,asc'
        } else {
          this.tableSort = ''
        }
      }

      this.initTableData()
    },

  },
  computed: {
    tableContentValue () {
      return (row, value) => {
        var result = ''
				if(value == 'status'){
					if(row[value] == 0){
						result = '启用'
					}else{
						result = '禁用'
					}
				}else if(value == 'updateDate'){
					if(row.startDate == null && row.endDate == null){
						result = ''
					}else{
						result = row.startDate + ' ~ ' +row.endDate
						// if( row.startDate){
							
						// }else{
						// 	result = ''
						// }
						
					}
				}
				else {
          result = row[value]
        }
				// console.log(result)
        return result
      }
    },

    tableSortable () {
      return (value) => {
        var result = ''
        if (
          value == 'runningAmount' ||
          value == 'comprehensiveCost' ||
          value == 'grossProfit' ||
          value == 'robotGrossProfit' ||
          value == 'netProfit'
        ) {
          result = 'custom'
        } else {
          result = false
        }

        return result
      }
    }
  }
}
</script>

